@import url(https://fonts.googleapis.com/css?family=Sulphur+Point&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Main {
	font-family: sans-serif;
	background: inherit;
	height: 50px;
}

.DropDownContainer {
	width: 10em;
	margin: 0 auto;
	z-index: 1;
}

.DropDownHeader {
	padding: 0.4em 1em 0.4em 1em;
	font-weight: 500;
	font-size: 1.3rem;
	color: #3faffa;
	background: inherit;
	cursor: pointer;
}

.DropDownList {
	padding: 0;
	margin: 0;
	margin-right: 1em;
	padding-left: 0.7em;
	padding-top: 1em;
	background: #ffffff;
	border: 2px solid #e5e5e5;
	border-radius: 20px;
	box-sizing: border-box;
	color: #3faffa;
	font-size: 1.3rem;
	font-weight: 500;
}

.DropDownBox {
	width: 10.5em;
	position: absolute;
	z-index: 2;
}

.ListItem {
	list-style: none;
	margin-bottom: 0.8em;
	vertical-align: middle;
}

.flagItem {
	cursor: pointer;
}

.flagItem:hover {
	scale: 1.1;
	color: #30d95d;
}

.flagItem img {
	display: inline-block;
}

.flagItem p {
	position: absolute;
	display: inline-block;
	margin: 0;
	padding-left: 15px;
	padding-top: 3px;
}

#darkMode {
	background: transparent url(/dainis-portfolio/static/media/moon.08c8560a.png) no-repeat center;
	background-size: 30px 30px;
	width: 45px;
	height: 45px;
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
	border: none;
	border-radius: 50%;
	background-color: white;
	transition: background-color 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
	transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out;
	transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
	margin: 1em 10%;
}

#darkMode:hover,
#darkMode:focus {
	-webkit-filter: none;
	        filter: none;
	background-color: black;
	cursor: pointer;
}

#darkMode.clicked {
	-webkit-filter: none !important;
	        filter: none !important;
	background-color: black;
}

#darkMode.clicked:hover {
	-webkit-filter: grayscale(100%) !important;
	        filter: grayscale(100%) !important;
	background-color: white;
}

#darkMode.clickedDark {
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
	background-color: white;
}

#darkMode.clickedDark:hover {
	-webkit-filter: none !important;
	        filter: none !important;
	background-color: black;
}

header {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	background-color: rgb(23, 36, 48);
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 1;
}

header h3 {
	color: white;
	margin: 0;
	padding: 1rem;
}

#homeLink a:hover {
	text-decoration: none;
	color: darkcyan;
}

header h5 {
	color: white;
	margin: 0;
	padding: 1rem;
}

header a {
	color: white;
}

#linkSpan a {
	display: block;
	text-decoration: none;
	padding: 1rem;
	font-size: larger;
}

#linkSpan a:hover {
	scale: 1.1;
	color: darkcyan;
}

#linkSpanInline {
	display: none;
}

@media only screen and (max-width: 991px) {
	header h5 {
		display: none;
	}

	#linkSpan {
		display: none;
	}

	#linkSpanInline {
		display: inline;
	}

	#linkSpanInline a {
		text-decoration: none;
		font-size: larger;
		margin-left: 1rem;
	}

	.overviewMain {
		width: 80%;
		margin: 10% auto;
	}
}

