a {
	color: #545454;
	text-decoration: none;
}

a:focus,
a:hover {
	color: #8b8b8b;
}

.loading {
	padding: 10px;
	-webkit-transform: translate3d(-50%, -50%, 0);
	        transform: translate3d(-50%, -50%, 0);
	color: #8b8b8b;
}

.scrollArea {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	overflow: auto;
}

.frame {
	position: absolute;
	width: 100%;
	bottom: 0;
	left: 0;
	padding: 1.5rem;
	z-index: 2;
}

.frame__title {
	font-size: inherit;
	font-weight: normal;
	margin: 0;
}

.frame__links {
	margin: 1rem 0;
}

.frame__link:not(:last-child) {
	margin-right: 1rem;
}

.frame__nav .frame__link:first-child {
	margin-right: 1.5rem;
}

.bottom-left {
	padding: 1.5rem;
	-webkit-transform: translate3d(0, -100%, 0) !important;
	        transform: translate3d(0, -100%, 0) !important;
}

.canvas > div {
	z-index: 10000000;
}

@media screen and (min-width: 53em) {
	.frame {
		display: grid;
		height: 100vh;
		padding: 2rem 3.7rem;
		pointer-events: none;
		-webkit-align-content: space-between;
		        align-content: space-between;
		grid-template-columns: 50% 50%;
		grid-template-rows: auto auto auto;
		grid-template-areas:
			"title links"
			"... ..."
			"... nav";
	}
	.frame__title {
		grid-area: title;
	}
	.frame__links {
		grid-area: links;
		justify-self: end;
		margin: 0;
	}
	.frame__link {
		pointer-events: auto;
	}
	.frame__nav {
		grid-area: nav;
		justify-self: end;
	}
	.bottom-left {
		padding: 0 0 2rem 3.7rem;
	}
}

*,
*::after,
*::before {
	box-sizing: border-box;
}

:root {
	--blue: #0c0f13;
	--white: rgb(236, 236, 236);
}

::selection {
	background: #2ddab8;
	color: white;
}

#root {
	width: 100vw;
	height: 100vh;
	overflow-x: scroll;
}

body {
	margin: 0;
	margin-top: 8rem;
	overflow: hidden;
	font-family: "Sulphur Point", sans-serif;
	background: var(--background-color);
	color: var(--text-color);
	font-weight: var(--font-weight);
	transition: background 0.3s ease-in-out, color 0.6s ease-in-out;
}

body.light {
	--background-color: var(--white);
	--text-color: var(--blue);
	--font-weight: 400;
}

body.dark {
	--background-color: var(--blue);
	--text-color: var(--white);
	--font-weight: 500;
}

